
'use client';

import * as React from 'react';
import { useRouter, useSearchParams } from 'next/navigation';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from '@/components/ui/card';
import {
  Table,
  TableBody,
  TableCell,
  TableHead,
  TableHeader,
  TableRow,
} from '@/components/ui/table';
import { AUDIENCE_DATA } from '@/lib/data';
import type { Audience } from '@/lib/types';
import { ArrowRight } from 'lucide-react';
import { Leaf } from 'lucide-react';


const DatasetPage = () => {
  const router = useRouter();
  const searchParams = useSearchParams();
  const audience = searchParams.get('audience') as Audience | null;
  const [dataset, setDataset] = React.useState<any[]>([]);

  React.useEffect(() => {
    if (audience && AUDIENCE_DATA[audience]) {
      const audienceData = AUDIENCE_DATA[audience];
      const getRandomItem = (arr: any[]) => arr[Math.floor(Math.random() * arr.length)];
      const generatedData = Array.from({ length: 25 }).map((_, i) => ({
        id: i + 1,
        feature1: getRandomItem(audienceData.features[0].values || []),
        feature2: getRandomItem(audienceData.features[1].values || []),
        feature3: getRandomItem(audienceData.features[2].values || []),
        target: Math.random() > 0.5 ? audienceData.target.labels[0] : audienceData.target.labels[1]
      }));
      setDataset(generatedData);
    } else {
        // Handle case where audience is not provided or invalid
        // Maybe redirect back to home
        router.push('/');
    }
  }, [audience, router]);

  if (!audience || !AUDIENCE_DATA[audience]) {
    return null; // or a loading state
  }

  const audienceData = AUDIENCE_DATA[audience];
  const Icon = AUDIENCE_DATA[audience].datasetSummaryIcon;

  return (
    <>
    <header className="sticky top-0 z-50 w-full border-b bg-background/95 backdrop-blur supports-[backdrop-filter]:bg-background/60">
      <div className="container flex h-16 items-center justify-between">
        <div className="flex items-center gap-3">
          <Leaf className="h-7 w-7 text-primary" />
          <h1 className="text-2xl font-headline font-bold text-foreground">
            Forest Insights
          </h1>
        </div>
      </div>
    </header>
    <main className="flex flex-col items-center justify-center min-h-[calc(100vh-4rem)] bg-background p-4 sm:p-6 md:p-8">
      <div className="max-w-4xl w-full">
        <Card className="shadow-xl">
          <CardHeader>
            <div className="flex justify-between items-center">
              <div>
                <CardTitle className="text-2xl font-headline">Generated Sample Data</CardTitle>
                <CardDescription>A synthetic dataset for the <strong className="text-primary">{audience}</strong> audience.</CardDescription>
              </div>
               <Icon className="h-8 w-8 text-muted-foreground" />
            </div>
          </CardHeader>
          <CardContent>
            <div className="max-h-96 overflow-y-auto border rounded-md mb-6">
              <Table>
                <TableHeader>
                  <TableRow>
                    {audienceData.features.map(f => <TableHead key={f.name}>{f.name}</TableHead>)}
                    <TableHead>{audienceData.target.name}</TableHead>
                  </TableRow>
                </TableHeader>
                <TableBody>
                  {dataset.map((row) => (
                    <TableRow key={row.id}>
                      <TableCell>{row.feature1}</TableCell>
                      <TableCell>{row.feature2}</TableCell>
                      <TableCell>{row.feature3}</TableCell>
                      <TableCell>{row.target}</TableCell>
                    </TableRow>
                  ))}
                </TableBody>
              </Table>
            </div>
            <div className="flex justify-end">
              <Button size="lg" onClick={() => router.push(`/?dashboard=true&audience=${audience}`)}>
                Explore the Algorithm <ArrowRight className="ml-2 h-5 w-5" />
              </Button>
            </div>
          </CardContent>
        </Card>
      </div>
    </main>
    </>
  );
};

export default function DatasetPageWrapper() {
  return (
    <React.Suspense fallback={<div>Loading...</div>}>
      <DatasetPage />
    </React.Suspense>
  );
}
